:root {
  --primary: #6366f1;
  --primary-dark: #4f46e5;
  --success: #22c55e;
  --danger: #ef4444;
  --warning: #f59e0b;
  --surface-0: #fff;
  --surface-50: #f8fafc;
  --surface-100: #f1f5f9;
  --surface-200: #e2e8f0;
  --surface-300: #cbd5e1;
  --surface-500: #64748b;
  --surface-700: #334155;
  --surface-900: #0f172a;
  --spacing-1: .25rem;
  --spacing-2: .5rem;
  --spacing-3: .75rem;
  --spacing-4: 1rem;
  --spacing-6: 1.5rem;
  --spacing-8: 2rem;
  --shadow-sm: 0 1px 2px #0000000d;
  --shadow-md: 0 4px 6px -1px #0000001a;
  --shadow-lg: 0 10px 15px -3px #0000001a;
  --radius-sm: .375rem;
  --radius-md: .5rem;
  --radius-lg: 1rem;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--surface-50);
  color: var(--surface-900);
  min-height: 100vh;
  font-family: Inter, sans-serif;
  line-height: 1.5;
}

.auth-screen {
  min-height: 100vh;
  padding: var(--spacing-4);
  background-color: var(--surface-50);
  justify-content: center;
  align-items: center;
  display: flex;
}

.auth-container {
  background: var(--surface-0);
  padding: var(--spacing-8);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  text-align: center;
  width: 100%;
  max-width: 400px;
}

.auth-container h1 {
  color: var(--surface-900);
  margin-bottom: var(--spacing-2);
  font-size: 1.875rem;
  font-weight: 700;
}

.auth-container p {
  color: var(--surface-500);
  margin-bottom: var(--spacing-6);
}

.auth-form {
  width: 100%;
  margin: 0 auto;
}

.form-group {
  margin-bottom: var(--spacing-4);
  text-align: left;
}

.form-group label {
  margin-bottom: var(--spacing-2);
  color: var(--surface-700);
  font-weight: 500;
  display: block;
}

.form-group input {
  border: 1px solid var(--surface-200);
  border-radius: var(--radius-md);
  background-color: var(--surface-0);
  width: 100%;
  padding: .75rem;
  font-size: 1rem;
  transition: all .2s;
}

.form-group input:focus {
  border-color: var(--primary);
  outline: none;
  box-shadow: 0 0 0 3px #6366f11a;
}

.btn-primary {
  background: var(--primary);
  color: #fff;
  width: 100%;
  padding: var(--spacing-3) var(--spacing-4);
  border-radius: var(--radius-md);
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-2);
  margin-bottom: var(--spacing-3);
  border: none;
  font-size: 1rem;
  font-weight: 500;
  transition: all .2s;
  display: inline-flex;
}

.btn-primary:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
}

.btn-secondary {
  background: var(--surface-0);
  width: 100%;
  color: var(--surface-900);
  border: 1px solid var(--surface-200);
  padding: var(--spacing-3) var(--spacing-4);
  border-radius: var(--radius-md);
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-2);
  margin-bottom: var(--spacing-3);
  font-size: 1rem;
  font-weight: 500;
  transition: all .2s;
  display: inline-flex;
}

.btn-secondary:hover {
  background: var(--surface-50);
  transform: translateY(-1px);
}

.divider {
  text-align: center;
  margin: var(--spacing-4) 0;
  color: var(--surface-500);
  position: relative;
}

.divider:before, .divider:after {
  content: "";
  background-color: var(--surface-200);
  width: 45%;
  height: 1px;
  position: absolute;
  top: 50%;
}

.divider:before {
  left: 0;
}

.divider:after {
  right: 0;
}

.auth-footer {
  margin-top: var(--spacing-6);
  text-align: center;
  color: var(--surface-500);
}

.auth-footer a {
  color: var(--primary);
  font-weight: 500;
  text-decoration: none;
}

.auth-footer a:hover {
  text-decoration: underline;
}

.biometric-setup {
  margin: var(--spacing-4) 0;
}

.checkbox-container {
  align-items: center;
  gap: var(--spacing-2);
  color: var(--surface-700);
  justify-content: center;
  display: flex;
}

.checkbox-container input[type="checkbox"] {
  border-radius: var(--radius-sm);
  border: 1px solid var(--surface-300);
  cursor: pointer;
  width: 1rem;
  height: 1rem;
}

.icon {
  width: 1.25rem;
  height: 1.25rem;
}

.error-message {
  color: var(--danger);
  margin-top: var(--spacing-1);
  text-align: left;
  font-size: .875rem;
}

.success-message {
  color: var(--success);
  margin-top: var(--spacing-1);
  text-align: left;
  font-size: .875rem;
}

.loading {
  opacity: .7;
  cursor: not-allowed;
}

.loading:after {
  content: "...";
  animation: 1s infinite loading;
}

@keyframes loading {
  0% {
    content: ".";
  }

  33% {
    content: "..";
  }

  66% {
    content: "...";
  }
}

@media (width <= 480px) {
  .auth-container {
    padding: var(--spacing-4);
    margin: var(--spacing-2);
  }

  .auth-container h1 {
    font-size: 1.5rem;
  }

  .btn-primary, .btn-secondary {
    padding: var(--spacing-2) var(--spacing-4);
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --surface-0: #1e293b;
    --surface-50: #0f172a;
    --surface-100: #1e293b;
    --surface-200: #334155;
    --surface-300: #475569;
    --surface-500: #94a3b8;
    --surface-700: #cbd5e1;
    --surface-900: #f8fafc;
  }

  .auth-container {
    background: var(--surface-100);
  }

  .form-group input {
    background: var(--surface-0);
    color: var(--surface-900);
    border-color: var(--surface-300);
  }

  .btn-secondary {
    background: var(--surface-200);
    color: var(--surface-900);
    border-color: var(--surface-300);
  }

  .btn-secondary:hover {
    background: var(--surface-300);
  }
}

:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

:focus:not(:focus-visible) {
  outline: none;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.auth-container {
  animation: .3s ease-out fadeIn;
}

#biometricAuthBtn {
  margin-top: var(--spacing-4);
  background: var(--primary);
  color: #fff;
  width: 100%;
  padding: var(--spacing-3) var(--spacing-4);
  border-radius: var(--radius-md);
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-2);
  border: none;
  font-weight: 500;
  transition: all .2s;
  display: inline-flex;
}

#biometricAuthBtn:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
}

#biometricAuthBtn:disabled {
  background: var(--surface-300);
  cursor: not-allowed;
  transform: none;
}

#googleLoginBtn, #googleSignupBtn {
  color: var(--surface-900);
  border: 1px solid var(--surface-200);
  background: #fff;
}

#googleLoginBtn:hover, #googleSignupBtn:hover {
  background: var(--surface-50);
}

.form-group input:invalid {
  border-color: var(--danger);
}

.form-group input:invalid:focus {
  box-shadow: 0 0 0 3px #ef44441a;
}

.validation-message {
  margin-top: var(--spacing-1);
  font-size: .875rem;
}

.validation-message.error {
  color: var(--danger);
}

.validation-message.success {
  color: var(--success);
}
